const timeago = stime => {
  const time = new Date(stime);
  const delta = parseInt((new Date() - time) / 1000, 10);
  if (delta <= 0) {
    return 'just now';
  }

  const minutes = delta / 60;
  if (minutes < 1) {
    return 'less than a minute ago';
  }
  if (minutes < 2) {
    return 'about a minute ago';
  }

  const hours = minutes / 60;
  if (hours < 1) {
    return `${parseInt(minutes, 10)} minutes ago`;
  }
  if (hours < 1.5) {
    return 'about an hour ago';
  }

  const days = hours / 24;
  if (days < 1) {
    return `${Math.round(hours)} hours ago`;
  }
  if (days < 7) {
    return `${parseInt(days, 10)} days ago`;
  }

  let month = time.getMonth();
  if (month < 10) {
    month = `0 ${month}`;
  }
  let date = time.getDate();
  if (date < 10) {
    date = `0 ${date}`;
  }
  return [time.getFullYear(), month, date].join('-');
};

const uncode = code => {
  const dcode = unescape(code);
  return dcode;
};
exports.timeago = timeago;
exports.uncode = uncode;



