<template>
  <div id="approot">
  <Headerv :u="u" :showright.sync="showright" :userinfo="userinfo" :s.sync="showlogin"></Headerv>
  <div class="mine" >
    <router-view></router-view>
  </div>
  <Footerv :u="u"></Footerv>
  <Mlogin :l="l" :s.sync="showlogin"></Mlogin>
  </div>
</template>

<script>
import Headerv from './components/Headerv.vue';
import Footerv from './components/Footerv.vue';
import Mlogin from './components/Mlogin.vue';
import store from './store';
const vdata = {
  u: '//www.erun360.com/',  // 默认url前缀 '//www.erun360.com/' || '/'
  l: window.location.href,
  showright: false,
  userinfo: store.userInfo(),
  showlogin: false,
};
export default {
  components: { Headerv, Footerv, Mlogin },
  data() {
    return vdata;
  },
  watch: {
    showlogin: {
      handler: (val) => {
        if (val === true) {
          const ge = self.setInterval(() => {
            if (store.userInfo().uid > 0) {
              vdata.showlogin = false;
              vdata.userinfo = store.userInfo();
              window.clearInterval(ge);
            }
          }, 1000);
        }
      },
    },
  },
};

</script>

<style>

</style>



