<template>
  <div class="debgbox" v-show="s" @click="hidelogin"></div>
  <iframe :src="url" v-show="s" id="login"></iframe>
</template>

<script>
export default {
  props: ['l', 's'],
  data() {
    return {
      url: `http://e.erun360.com/Member/minilogin.aspx?back=${this.l}`,
    };
  },
  methods: {
    hidelogin() {
      this.s = false;
    },
  },
};
</script>
<style scoped>
.debgbox {
    position: fixed;
    z-index: 1;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(38,38,38,.4);
}
#login {
    position: fixed;
    z-index: 11;
    top: 200px;
    left: 50%;
    width: 430px;
    height: 330px;
    margin-left: -215px;
    border-radius: 5px;
    background: #fff;
    box-shadow: 0 0 10px rgba(0,0,0,.25);
}
</style>



